//import  {}  from './draw'
import NebPay from 'nebpay.js'
let daily = require('./draw.js').daily
const nebPay = new NebPay()
// const nebTo = 'n1bs9seJ2QtqY7w28MkRTUkosciEwgCD4Fo' // testnet
const nebTo = 'n1Pgz5NeWd67ukEpw5a5Jzt1Gb4TM4c49GY' // mainnet
const mainnetUrl = "https://pay.nebulas.io/api/mainnet/pay"
const testnetUrl = "https://pay.nebulas.io/api/pay"

export default {
  components: {

  },
  data () {
    return {
      time:{
        month:Number,
        date:Number,
        hours:Number,
      },
      msg:"Choose one",
      startTouchTime:0,
      draw:false,
      path:'',
      mean:'',
      card:'',
      style:'',
    }
  },
  created(){
    let rc = this.$ls.get('daily','nothing')
    if(rc=='nothing') return;
    console.log(rc)
    this.render(rc);
  }
  ,
  methods:{
    leaveHandler(){
      history.back();
    },
    render(d){

      if(d.reversed) this.style='transform: scaleY(-1);'
      this.path = d.path;
      this.mean = d.mean;
      this.card = d.card;
      this.time = d.time;
      this.msg="Go Back"
      this.draw = true;
    },
    onBegin(e) {
      nebPay.pay(nebTo, '0', {
        callback: mainnetUrl,
        listener: () => this.tEnd(e)
      })
    },
    tStart(e){
      this.startTouchTime = e.timeStamp;
      this.size+=50;
    },
    tEnd(e){
      let touchtime = (e.timeStamp-this.startTouchTime)/1000;
      //if(touchTime<1) return
      let d = daily();
      let now = new Date;
      d.time = {
            'month':now.getMonth()+1,//差一个月
            'date':now.getDate(),
            'hours':now.getHours()
      };
//      console.log(d)
      this.render(d);
      this.$ls.set(
        'daily',{
          'time':this.time,
          'card':d.card,
          'path':d.path,
          'reversed':d.reversed,
          'mean':d.mean
        },24*60*60*1000);//a day expire
    }
  }
}



